import React, { Component } from 'react';
import Breadcrumb from 'antd/es/breadcrumb';
import cls from 'classnames';
import { Scrollbars } from 'react-custom-scrollbars';
const { Item } = Breadcrumb;
export default class ExtBreadcrumb extends Component {
    constructor() {
        super(...arguments);
        this.getBreadcrumbItems = () => {
            const { pathData } = this.props;
            return pathData.map(item => (<Item key={item.key} href={item.path}>
        {item.name}
      </Item>));
        };
    }
    render() {
        const { style, className, children, pathData, rightExtra, rightExtraClass, extra, extraClassName, extraStyle, autoScroll, hidden, } = this.props;
        const hasHead = extra || pathData || rightExtra;
        return (<div className={cls('page-breadcrumb', { 'no-breadcrumb': !pathData && !rightExtra })} hidden={hidden}>
        {pathData || rightExtra ? (<div className="breadcrumb-box">
            {pathData && pathData.length ? (<Breadcrumb>{this.getBreadcrumbItems()}</Breadcrumb>) : null}
            {rightExtra ? (<div className={cls(rightExtraClass, 'bread-right-extra')}>{rightExtra}</div>) : null}
          </div>) : null}
        {extra ? (<div style={extraStyle} className={cls(extraClassName, 'bread-extra')}>
            {extra}
          </div>) : null}
        <div className={cls(className, 'page-box', {
            'page-box-head': hasHead,
            'page-box-no-head': !hasHead,
        })} style={style}>
          {autoScroll ? <Scrollbars autoHide>{children}</Scrollbars> : children}
        </div>
      </div>);
    }
}
ExtBreadcrumb.defaultProps = {
    pathData: [],
    autoScroll: true,
    rightExtra: null,
    rightExtraClass: '',
};
